// 
// "YYYY-mm-dd HH:MM"
export function dateFormatL(fmt, date) {
	let ret;
	const opt = {
		"Y+": date.getFullYear().toString(), // 
		"m+": (date.getMonth() + 1).toString(), // 
		"d+": date.getDate().toString(), // 
		"H+": date.getHours().toString(), // 
		"M+": date.getMinutes().toString(), // 
		"S+": date.getSeconds().toString() // 
		// 
	};
	for (let k in opt) {
		ret = new RegExp("(" + k + ")").exec(fmt);
		if (ret) {
			fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
		};
	};
	return fmt;
}
export default {
	/**
	 * toast
	 * @param {*} config
	 * showToast('')
	 * showToast({icon: 'none', title: ''})
	 */
	showToast(config = {}) {
		if (typeof config == 'string') {
			config = {
				title: config,
				icon: 'none'
			}
		}
		uni.showToast(config)
	},

	/**
	 * 
	 * @param {config} url:   type: 'page', 'tab'
	 * go("/pages/home/index")
	 * go({url: '/pages/home/index'})
	 */
	 go(config = {}) {
		if (typeof config == 'string') {
			config = {
				url: config
			}
		}
		const {url, type = "page"} = config
		let options = {
			url,
			success: res => {},
			fail: res => {
				console.log("", res)
			},
			complete: res => {}
		}
		switch (type) {
			case 'page':
				uni.navigateTo(options);
				break;
			case 'tab':
				uni.switchTab(options)
				break;
            case 'rep':
                uni.redirectTo(options);    
		}
	},

	/**
	 * 
	 * $util.makePhoneCall('123')
	 */
	makePhoneCall(config = {}) {
		if (typeof config == 'string') {
			config = {
				phoneNumber: config
			}
		}
		uni.makePhoneCall(config);
	},

	/**
	 * 
	 * back()
	 * back({delta: 1})
	 *
	 */
	back(config = {}) {
		if (typeof config == 'string') {
			config = {
				delta: config || 1
			}
		}
		uni.navigateBack({
			...config
		})
	},
	/**
	 * 
	 * @param {Object} config
	 */
	setNavigationBarTitle(config) {
		if (typeof config == 'string') {
			config = {
				title: config
			}
		}
		uni.setNavigationBarTitle(config);
	},

	/**
	 * 
	 */
	 getPrevPage() {
		return new Promise((resolve, reject) => {
			var pages = getCurrentPages();
			var Page = pages[pages.length - 1];//
			var prevPage = pages[pages.length - 2];  //
			if(prevPage) {
				resolve(prevPage)
			}
		})
	},

	// 
	getUrlQuery(url, name) {
	  var reg = new RegExp("(^|&|/?)"+ name +"=([^&]*)(&|$)");
	    var r = url.match(reg);
	    console.log(`${url} ${name} ${r}`)
	    if(r!=null) {
	      let str = decodeURIComponent(r[2])
	      return isNaN(Number(str))? str: Number(str)
	    } else {
	      return null;
	    }
	},
    // 
    validatorDefaultCatch(err, type = "message") {
        uni.showToast({
            title: err.errors[0].message,
            icon: 'none',
            duration: 2000
        })
        return false
    }
}